#body, #wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    font-size: 18px;

    #log {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        color: #fff;
        padding: 5px;
        font-size: 13px;
        line-height: 16px;
        word-break: break-all;
        background-color: #304767;
    }
}

.form {
    background-color: #fff;
    padding: 30px 20px;
    width: 100%;
    max-width: 560px;

    .form__container {
        max-width: 420px;
        margin: 0 auto;

        .form__steps {
            margin: 0 0 30px 0;
        }

        .form__field {
            width: 100%;
            margin: 15px 0;

            .field__title {
                display: flex;
                line-height: 35px;
                margin: 0;
                font-size: 0.9em;
                color: #40546c;
            }
            
            .field__error {
                margin: 0 0 0 auto;
                color: #e53f3f;
            }
            
            .field__input, .field__select {
                box-sizing: border-box;
                width: 100%;
                height: 40px;
                font-size: 0.9em;
                padding: 0 10px;
                border: 1px solid #e3e9ec;
                border-radius: 5px;
                appearance: none;
                -moz-appearance: none;
                -webkit-appearance: none;
            
                .select__wrapper {
                    width: 100%;
                    position: relative;
                }
            
                .select__wrapper:after {
                    content: "";
                    position: absolute;
                    border: 5px solid transparent;
                    border-top: 6px solid #7c8b94;
                    z-index: 5;
                    top: 18px;
                    right: 10px;
                }
            
                .field__input:focus, .field__select:focus {
                    outline: none;
                    -webkit-box-shadow: 0px 0px 3px 0px #0b7bff;
                    -moz-box-shadow: 0px 0px 3px 0px #0b7bff;
                    box-shadow: 0px 0px 4px 0px #0b7bff;
                    border-color: #0b7bff;
                }
            }
        }

        .form__send {
            width: 100%;
            background-color: #0075ff;
            color: #fff;
            cursor: pointer;
            outline: none;
            letter-spacing: 1px;
            border: 0;
            height: 40px;
            margin: 10px 0;
            border-radius: 5px;
            font-size: 1em;
        }
        
        .form__send:hover {
            filter: opacity(0.8);
        }

        .form_success {
            margin: 70px 0;
            text-align: center;
        
            .form_success__title {
                color: #4c5b68;
                margin: 0;
            }
        
            .formsuccess__text {
                line-height: 24px;
                margin: 26px 0;
                font-weight: 100;
                color: #94a1ac;
            }
        
            .formsuccess__contact {
                line-height: 22px;
                margin: 0;
            }
        
            .formsuccess__link {
                color: cornflowerblue;
                margin: 0 5px;
                text-decoration: underline;
            }
        }
    }
}

.steps {
    width: 100%;
    display: flex;
    position: relative;
    justify-content: space-between;

    .steps__arrow_container {
        position: absolute;
        margin: 15px 0;
        width: calc(100% - 50px);
        left: 25px;
        display: flex;
        justify-content: flex-start;
        height: 1px;
        background-color: #b3d6ff;

        .steps__arrow {
            margin: 0;
            border: 0;
            border-bottom: 1px solid #0075ff;
        }
    }

    .steps__step {
        display: flex;
        justify-content: center;
        position: relative;
        padding: 40px 0 0 0;
        color: #0075ff;
        width: 50px;
        font-size: 13px;
    }

    .steps__step:not(.active) {
        color: #b2d6ff;
    }

    .steps__step:after {
        content: "";
        position: absolute;
        border: 16px solid #0075ff;
        border-radius: 50%;
        top: 0;
    }
    
    .steps__step:not(.active):after{
        border-color: #b2d6ff;   
    }

    .steps__step:before {
        content: "";
        position: absolute;
        border: 6px solid #ffffff;
        border-radius: 50%;
        top: 10px;
        z-index: 1;
    }
}






